.listagem-salas {
    align-items: left;
    display: flex;
    flex-wrap: wrap;
    width:auto;
}


.bloco {
    width: 170px;
    height:170px;
    color:#4D4F53 ;
    font-weight: bold;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: rgb(255, 255, 255);
    font-size: 70px;
    text-align: center;
    line-height: 130px ;
    margin-left:15px;
    -webkit-user-select: none; 
    -ms-user-select: none; 
    user-select: none;
    transition: 500ms ease-in-out;
}

.bloco:active{
  top:5px;
  opacity:0.7;
}


.bloco:nth-child(1) { background-color: #FFF1D0; }
.bloco:nth-child(2) { background-color: #D5D1F1; }
.bloco:nth-child(3) { background-color: #FFCEC2; }
.bloco:nth-child(4) { background-color: #90D9CF; }
.bloco:nth-child(5) { background-color: #99AAF9; }
.bloco:nth-child(6) { background-color: #FFCBDB; }
.bloco:nth-child(7) { background-color: #E6E6E7; }

.btn-back {
  position: relative;
  left:88%;
  top:-10px;
  float: right;
  width: 100px;
}
